import React from 'react';
import recycle from './icons/recycle.svg';
import './App.css';
import Choose from './Choose';

window.onload = () => {
  fetch("https://api.airtable.com/v0/apprGW7sht9BtA4p0/stats?api_key=keyKiYMSWGx1Ckg0T", {
    method: "POST",
    headers: {
      'content-type': 'application/json'
    },
    body: JSON.stringify({
      fields: {
        Access: "x",
        
      }
    })
  })
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      route: "start",
      c1: "organico",
      c2: "altro0",
      status: {
        fine: false,
        text: ""
      }
    };
  }

  reroute = (route) => {
    this.setState({
      route: route
    });
  }

  goDeep = (c) => {
    var c1 = "";
    var c2 = "";
    switch (c) {
      case "organico":
        c1 = "umido";
        c2 = "verde";
        break;

      case "umido":
        c1 = "umido";
        c2 = "umido";
        break;

      case "verde":
        c1 = "verde";
        c2 = "verde";
        break;

      case "altro0":
        c1 = "vetro";
        c2 = "altro1";
        break;

      case "vetro":
        c1 = "vetro";
        c2 = "vetro";
        break;

      case "altro1":
        c1 = "carta";
        c2 = "altro2";
        break;

      case "carta":
        c1 = "carta pulita";
        c2 = "carta sporca";
        break;
      case "carta pulita":
        c1 = "carta pulita";
        c2 = "carta pulita"
        break;
      case "carta sporca":
        c1 = "carta sporca";
        c2 = "carta sporca";
        break;
      case "altro2":
        c1 = "multipack";
        c2 = "secco";
        break;
      case "secco":
        c1 = "secco";
        c2 = "secco";
        break;
      case "multipack":
        c1 = "metallo";
        c2 = "plastica";
        break;
      case "metallo":
        c1 = "ferrame";
        c2 = "metallo1";
        break;
      case "metallo1":
        c1 = "metallo1";
        c2 = "metallo1";
        break;
      case "ferrame":
        c1 = "ferrame";
        c2 = "ferrame";
        break;
      case "plastica":
        c1 = "plastica rigida";
        c2 = "altro3";
        break;
      case "plastica pulita":
        c1 = "plastica pulita";
        c2 = "plastica pulita";
        break;
      case "altro3":
        c1 = "plastica sporca";
        c2 = "plastica pulita";
        break;
      case "plastica sporca":
        c1 = "plastica sporca";
        c2 = "plastica sporca";
        break;
      case "plastica rigida":
        c1 = "plastica rigida";
        c2 = "plastica rigida";
        break;
    }
    this.setState({
      c1: c1,
      c2: c2
    });
  }
  render() {
    switch (this.state.route) {
      case 'start':
        return (
          <div className="App">
            <header className="App-header">
              <img src={recycle} className="App-logo" alt="something goes wrong" />
              <p>
                <button className="button button1" onClick={() => {
                  this.setState({
                    route: "1",
                    c1: "organico",
                    c2: "altro0",
                  });
                }}>Inizia!</button>
              </p>
              <p>Se non trovi dove poter buttare il tuo rifiuto magari è perche è da portare in discarica!
                Se hai ulteriori dubbi vai sul dito di <a href="https://www.cemambiente.it/dove-lo-butto/">cem ambiente</a></p>
            </header>
          </div>
        );
      case '1':
        return (
          < Choose reroute={this.reroute} c1={this.state.c1} c2={this.state.c2} goDeep={this.goDeep} status={this.state.status} />
        );
    }
  }
}

export default App;
